import React, {useState} from "react";
import {Link} from "react-router-dom";
import styles from "./Nav.module.css";
//Images
import Logo from "./../../../Images/Logo.svg";
import Arrow from "./../../../Images/Icons/Back/Back.svg";
import Menu from "./../../../Images/Icons/Burger/Burger.svg";

function Nav(){
    //State
    const [navStatus, setNavStatus] = useState(false);
    //Methods
    const navToggle = () => {
        setNavStatus(!navStatus);
    }

    //Styles
    let conditionalStyle;
    if(navStatus){
        conditionalStyle = styles.shown;
    } else {
        conditionalStyle = styles.hidden;
    }

    const slideWindowStyle = {
        width: 0
    }
    const fadeStyle = {
        opacity: 0,
        zIndex: 90,
        visibility: "hidden"
    }
    const removeBackgrounStyle = {
        backgroundColor: "transparent",
        width: 0
    }

    return (
        <div className={`${styles.outerContainer}`} style={navStatus? null : removeBackgrounStyle}>
            <img 
                src={Menu} 
                className={`${styles.menuBtn}`} 
                onClick={() => navToggle()}
                style={navStatus ? fadeStyle : null}
            ></img>
            <div className={`${styles.navBar}`} style={navStatus? null : slideWindowStyle}>
                <div className={`${styles.topBar} ${conditionalStyle}`}>
                    <img src={Logo} className={styles.logo}></img>
                    <img src={Arrow} className={styles.arrow} onClick={() => navToggle()}></img>
                </div>
                <div className={`${styles.linkContainer} ${conditionalStyle}`}>
                    <Link className={styles.navLinks} to="/">OCR</Link>
                    <Link className={styles.navLinks} to="/projects">About</Link>
                </div>
            </div>
        </div>
        
    )
}

export default Nav;