import React, {useState, useRef, useEffect} from "react";
import {Rnd} from "react-rnd";
import styles from "./Home.module.css";
//Components
import { useUserMedia } from './Camera/useUserMedia';
import Nav from "./../../Nav/Nav";

//Images
import Capture from "./../../../../Images/Icons/Camera/Camera.svg";
import Crop from "./../../../../Images/Icons/Crop/Crop.svg";
import Upload from "./../../../../Images/Icons/Upload/Upload.svg";

//Options
const CAPTURE_OPTIONS = {
    audio: false,
    video: { facingMode: "environment" },
};

//Helper Funcionts
function getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
}

function cropCanvas(sourceCanvas,left,top,width,height) {
    let destCanvas = document.createElement('canvas');
    destCanvas.width = width;
    destCanvas.height = height;
    destCanvas.getContext("2d").drawImage(
        sourceCanvas,
        left,top,width,height,  // source rect with content to crop
        0,0,width,height);      // newCanvas, same size as source rect
    return destCanvas;
}

//Home Component
function Home(props){
    //State
    const [popUpStatus, setPopUpStatus] = useState(false);
    const [cropToolStatus, setCropToolStatus] = useState(false);
    const [cropTool, setCropToolProperties] = useState({
        size: {
            width: (window.innerWidth*.30),
            height: (window.innerHeight*.15),
        },
        position:{
            x: ( window.innerWidth/2 - ((window.innerWidth*.30) / 2) ),
            y: ( window.innerHeight/2 - ((window.innerHeight*.15) / 2) )
        }
    })
    const [text, setText] = useState("")
    const videoRef = useRef(null);
    const mediaStream = useUserMedia(CAPTURE_OPTIONS);

    if (mediaStream && videoRef.current && !videoRef.current.srcObject) {
        videoRef.current.srcObject = mediaStream;
    }

    //Methods
    const toggleCropTool = () => {
        setCropToolStatus(!cropToolStatus);
    }
    const handleCanPlay = () => {
        videoRef.current.play();
    }
    const onCapture = () => {
        if(typeof cropTool.size.width == "string"){
            const tempObject = {
                position:{
                    ...cropTool.position
                },
                size:{
                    width: cropTool.size.width.substring(0, cropTool.size.width.length-2),
                    height: cropTool.size.height.substring(0, cropTool.size.height.length-2)
                }
            }
            setCropToolProperties(tempObject);
        }
        //Creating new canvas and matching dimensions of video]
        let tempCanvas = document.createElement("canvas");
        tempCanvas.width = videoRef.current.videoWidth;
        tempCanvas.height = videoRef.current.videoHeight;
        //Actual Size of Video After CSS
        let clientRect = videoRef.current.getBoundingClientRect();
        let difference = (clientRect.width - window.innerWidth)/ 2;
       
        //Calculating position desired image based on cropTool
        let sx = (tempCanvas.width * ((cropTool.position.x+difference)/clientRect.width));
        let sy = tempCanvas.height * (cropTool.position.y/window.innerHeight);

        let width = (tempCanvas.width * (cropTool.size.width/clientRect.width));
        let height = tempCanvas.height * (cropTool.size.height/window.innerHeight);

        tempCanvas.getContext("2d").drawImage(videoRef.current,
            sx, 
            sy, 
            width, 
            height,
            0,
            0,
            width,
            height);
        tempCanvas = cropCanvas(tempCanvas, 0, 0, width, height);
        onFileUpload(tempCanvas.toDataURL("img/png"));
    }

    const onFileChange = (event) => {
        getBase64(event.target.files[0])
        .then(data => {
            onFileUpload(data);
        })
        .catch(err => Error(err))
    }

    const onFileUpload = (selectedFile) => {
        fetch("/api/v1/upload", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
            },
            body: selectedFile
        })
        .then(response => response.json())
        .then(data => {
            console.log(data);
            setPopUpStatus(true);
            setText(data.text);
        })
        .catch(e => {
            console.log(e);
            setText("Offline");
        })
    }

    const dismissPopUp = () => {
        setPopUpStatus(false);
        setTimeout(() => {
            setText("");
        }, 500);
    }

    //Conditional Style
    let popUpStyle = {
        opacity: 0,
        visibility: "hidden"
    };
    let cropToolStyle = {
        opacity: 0,
        visibility: "hidden"
    }

    //Rendering
    return (
    <div className={styles.outer}>
        <Rnd 
            style={cropToolStatus ? null : cropToolStyle}
            className={styles.cropTool}
            size={cropTool.size}
            position={cropTool.position}
            onDragStop={(e, d) => {setCropToolProperties((current) => {
                if(d.x+cropTool.size.width < window.innerWidth && 
                d.y+cropTool.size.height < window.innerHeight &&
                d.y > 0 &&
                d.x > 0){
                    return {
                        ...current,
                        position: {
                            x: d.x,
                            y: d.y
                    }
                }
                } else {
                    return {
                        ...current
                    }
                }
                
            })}}
            onResizeStop={(e, direction, ref, delta, position) => {setCropToolProperties((current) => {
                const curWidth = parseFloat(ref.style.width.substring(0, ref.style.width.length-2));
                const curHeight = parseFloat(ref.style.height.substring(0, ref.style.height.length-2));
                if(position.x+curWidth < window.innerWidth && 
                position.y+curHeight < window.innerHeight &&
                position.x > 0 &&
                position.y > 0){
                    return {
                        size: {
                            width: curWidth,
                            height:  curHeight,
                        },
                        position: {...position}
                    }
                } else {
                    return {
                        ...current
                    }
                }
            })}}
        />
        <div className={styles.homeContainer}>
            <Nav></Nav>
                <div 
                    className={styles.popUp} 
                    style={popUpStatus ? null : popUpStyle}
                    >
                    <p>{text}</p>
                    <span 
                        className={styles.dismissBtn}
                        onClick={dismissPopUp}
                    >Dismiss</span>
                </div>
                <video ref={videoRef} className={styles.video} onCanPlay={handleCanPlay} autoPlay playsInline muted />
            <div className={styles.controls}>
                <input type="file" id="fileUpload" onChange={onFileChange}></input>
                <img src={Crop} className={styles.cropBtn} onClick={toggleCropTool}></img>
                <img src={Capture} className={styles.captureBtn} onClick={onCapture}></img>
                <label htmlFor="fileUpload">
                    <img src={Upload} className={styles.uploadBtn}></img>
                </label>
            </div>
        </div>
    </div>
        
    )
}

export default Home;